import React from 'react';
import "./Skills.scss"
import {MdRestaurant} from "react-icons/md";
import html5_png from "../../../assets/imgs/html5.png";
import css3_png from "../../../assets/imgs/css3.png";
import javascript_png from "../../../assets/imgs/javascript.png";
import jQuery_png from "../../../assets/imgs/jQuery.png";
import react_png from "../../../assets/imgs/react.png";
import java_png from "../../../assets/imgs/java.png";
import mysql_png from "../../../assets/imgs/mysql.png";
import maria_png from "../../../assets/imgs/maria.png";
import mssql_png from "../../../assets/imgs/mssql.png";
import mongoDB_png from "../../../assets/imgs/mongoDB.png";
import tomcat_png from "../../../assets/imgs/tomcat.png";
import git_png from "../../../assets/imgs/git.png";
import docker_png from "../../../assets/imgs/docker.png";


const Skills = () => {
    return (
        <div className="Skills">
            <div className="title">
                <h3>
                    <span className="logo"><MdRestaurant size="40" /> <b>Skill</b></span>
                </h3>
            </div>
            <div className="category">
                <div className="item languages">
                    <div className="category_title"><h3>Front-End</h3></div>
                    <div className="iconBox"><img src={html5_png} alt="HTML5" /></div>
                    <div className="iconBox"><img src={css3_png} alt="CSS5" /></div>
                    <div className="iconBox"><img src={javascript_png} alt="Javascript" /></div>
                    <div className="iconBox"><img src={jQuery_png} alt="jQuery" /></div>
                    <div className="iconBox"><img src={react_png} alt="React" /></div>
                </div>
                <div className="item databases">
                    <div className="category_title"><h3>Back-End</h3></div>
                    <div className="iconBox"><img src={java_png} alt="Java" /></div>
                    <div className="iconBox"><img src={mysql_png} alt="Mysql" /></div>
                    <div className="iconBox"><img src={maria_png} alt="Maria" /></div>
                    <div className="iconBox"><img src={mssql_png} alt="Mssql" /></div>
                    <div className="iconBox"><img src={mongoDB_png} alt="MongoDB" /></div>
                </div>
                <div className="item server">
                    <div className="category_title"><h3>Devops</h3></div>
                    <div className="iconBox"><img src={tomcat_png} alt="Tomcat" /></div>
                    <div className="iconBox"><img src={git_png} alt="GIT" /></div>
                    <div className="iconBox"><img src={docker_png} alt="Docker" /></div>
                </div>
            </div>            
        </div>
    )
};

export default Skills;